//POPUP


/* Styles for dialog window */
#small-dialog {
	background: white;
	padding: 20px 30px;
	text-align: left;
	max-width: 400px;
	margin: 40px auto;
	position: relative;
}


/**
 * Fade-zoom animation for first dialog
 */

/* start state */
.my-mfp-zoom-in .zoom-anim-dialog {
	opacity: 0;

	-webkit-transition: all 0.2s ease-in-out; 
	-moz-transition: all 0.2s ease-in-out; 
	-o-transition: all 0.2s ease-in-out; 
	transition: all 0.2s ease-in-out; 



	-webkit-transform: scale(0.8); 
	-moz-transform: scale(0.8); 
	-ms-transform: scale(0.8); 
	-o-transform: scale(0.8); 
	transform: scale(0.8); 
}

/* animate in */
.my-mfp-zoom-in.mfp-ready .zoom-anim-dialog {
	opacity: 1;

	-webkit-transform: scale(1); 
	-moz-transform: scale(1); 
	-ms-transform: scale(1); 
	-o-transform: scale(1); 
	transform: scale(1); 
}

/* animate out */
.my-mfp-zoom-in.mfp-removing .zoom-anim-dialog {
	-webkit-transform: scale(0.8); 
	-moz-transform: scale(0.8); 
	-ms-transform: scale(0.8); 
	-o-transform: scale(0.8); 
	transform: scale(0.8); 

	opacity: 0;
}

/* Dark overlay, start state */
.my-mfp-zoom-in.mfp-bg {
	opacity: 0;
	-webkit-transition: opacity 0.3s ease-out; 
	-moz-transition: opacity 0.3s ease-out; 
	-o-transition: opacity 0.3s ease-out; 
	transition: opacity 0.3s ease-out;
}
/* animate in */
.my-mfp-zoom-in.mfp-ready.mfp-bg {
	opacity: 0.75;
}
/* animate out */
.my-mfp-zoom-in.mfp-removing.mfp-bg {
	opacity: 0;
}

.mfp-close-btn-in .mfp-close {
	top: 7px;
	right: 15px;
	opacity: 1;
	color: transparent;
	&:hover:before {
		transform: rotate(180deg);
	}
	&:hover:after {
		transform: rotate(0deg);
	}

	&:before, &:after {
		content: "";
		position: absolute;
		width: 35px;
		height: 4px;
		background: #000;
		top: 50%;
		left:5px;
	}

	&:before {
		transform: rotate(135deg);
		transition: all 0.2s linear;
	}

	&:after {
		transform: rotate(45deg);
		transition: all 0.2s linear;
	}
}




/**
 * Fade-move animation for second dialog
 */

/* at start */
.my-mfp-slide-bottom .zoom-anim-dialog {
	opacity: 0;
	-webkit-transition: all 0.2s ease-out;
	-moz-transition: all 0.2s ease-out;
	-o-transition: all 0.2s ease-out;
	transition: all 0.2s ease-out;

	-webkit-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	-moz-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	-ms-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	-o-transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );
	transform: translateY(-20px) perspective( 600px ) rotateX( 10deg );

}

/* animate in */
.my-mfp-slide-bottom.mfp-ready .zoom-anim-dialog {
	opacity: 1;
	-webkit-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	-moz-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	-ms-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	-o-transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
	transform: translateY(0) perspective( 600px ) rotateX( 0 ); 
}

/* animate out */
.my-mfp-slide-bottom.mfp-removing .zoom-anim-dialog {
	opacity: 0;

	-webkit-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	-moz-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	-ms-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	-o-transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
	transform: translateY(-10px) perspective( 600px ) rotateX( 10deg ); 
}

/* Dark overlay, start state */
.my-mfp-slide-bottom.mfp-bg {
	opacity: 0;

	-webkit-transition: opacity 0.3s ease-out; 
	-moz-transition: opacity 0.3s ease-out; 
	-o-transition: opacity 0.3s ease-out; 
	transition: opacity 0.3s ease-out;
}
/* animate in */
.my-mfp-slide-bottom.mfp-ready.mfp-bg {
	opacity: 0.8;
}
/* animate out */
.my-mfp-slide-bottom.mfp-removing.mfp-bg {
	opacity: 0;
}




// POPUP COMMON Settings






.myPopup {
	max-width: 745px;
	margin: 0 auto;
	width: 100%;
	.main-btn {
		color: #fff;
	}

	.center {
		background: #f6f6f6;
		background-size: cover;
			padding-top: (138%/740*100);
		padding-bottom: (109%/740*100);
		padding-left: (86%/740*100);
		padding-right: (86%/740*100);
		text-align: center;
		color: #000;
	}

	.popup_form {
		.footer_btn {
			margin-top: 12px;
		}
		.flex();
		flex-direction: column;
		justify-content: center;
		width: 100%;
		.text-field {
			border: none;
			max-width: 100%;
			text-align: center;
			
			width: 100%;
			color: #000;
			&::-webkit-input-placeholder {
    		color: #000;
    	
    }

     &::-moz-input-placeholder {
    	color: #000;
    }

    & {
			margin-bottom: 40px;
    	}
		}
	}

	
}


.popup_title {
	margin-bottom: 17px;
	&:before {
		content: none;
	}
}


.popup_description {
	margin-bottom: 30px;
	font-size: 16px;
}

.drinks_popup {
	.popup_img-layer {
		margin: 0 auto;
		width: 100%;
		height: 100%;
		background: #000;
		position: relative;
		&:before {
			content: "";
			display: block;
			padding-top: 100%;
		}
	}

	.popup_text {
		margin-top: 20px;
		text-align: left;
		line-height: 1.35;
	}
}

.thanks_popup.myPopup {
	.center {
		padding-top: (138px/740*100);
		padding-bottom: (109px/740*100);
		padding-left: (86px/740*100);
		padding-right: (86px/740*100);
	}

	.h2_title {
		word-spacing: -0.2px;
	}
	
	max-width: 700px;
	.footer_btn {
		margin: 	0 auto;
		max-width: 105px;
	}

	.popup_description {
		margin-bottom: 	27px;
		font-size: 21px;
	}

	.popup_answer {
		
		font-size: 21px;
		max-width: 420px;
		margin: 	0 auto;
		margin-bottom: 	30px;
	}

}