.catalog {
	.f-container {}
	margin-top: 90px;
	margin-bottom: 90px;
}


.catalog__card {

	margin: 0;
	margin-top: 31px;
	cursor: pointer;
	flex: 0 1 32%;
	min-width: 310px;
	transition: all 0.365s ease-in;
	box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
	&:hover {
		transform: scale(1.06);
		.catalog__card-name {
			color: #b1a26d;
		}
	}
}

.catalog__img {
	width: 100%;
}

.catalog__card-name {
	text-align: center;
	font-size: 18px;
	padding-top: 32px;
	padding-bottom: 35px;
	padding-left: 20px;
	padding-right: 20px;
	transition: all 0.25s ease-in;
	letter-spacing: 0.1px;
}