@import url('app/less/_fonts.less');


html, body {
	overflow-x: hidden; 

}
body 
{
	margin: 0;
	padding: 0;	
	font-family: 'Roboto', sans-serif;
	font-size: 16px;
	font-weight: 400;
	letter-spacing: 1.15px;



}

div, a {
	outline: none;
}



*, *:before, *:after {
	box-sizing: border-box;
}

img
{
	max-width: 100%;
	display: block;
}

h1, h2, h3, h4, h5, h6, p {
	padding: 0;
	margin: 0;
}

h2 {

	overflow-wrap: break-word;
}


ul , li {
	list-style: none;
	padding: 0;
	margin: 0;
}

a {
	text-decoration: none;
	padding: 0;
	margin: 0;
	display: block;
	color: #fff;
	cursor: pointer;

}


input {
	width: 100%;
	outline: none;
	border: none;
	text-align: center;
}


main {
	color: #000;
}

.center {
	max-width: 1220px;
	width: 100%;
	margin: 0 auto;
	padding: 0 25px;

}



.f-container {

	display: flex;
	justify-content: space-between;
	align-items: center;
	flex-wrap: wrap;
	flex-direction: row;
}


.flex(@justify-content: space-between, @align-items: center, @flex-direction: row, @flex-wrap: nowrap){
	display: flex;
	justify-content: @justify-content;
	align-items:  @align-items;
	flex-direction:  @flex-direction;
	flex-wrap:  @flex-wrap;
}




.header {
	.center {
		color: #242424;
	}

	a {
		color: #242424;
	}

	.f-container {
		flex-direction: column;
	}
	
}

.header__top, .header__bottom {
	.flex();
	width: 100%;
}

.header__menu, .header__menu-list {
	width: 100%;
}

.header__menu-list {
	.flex();
}

.header__menu-item {
	cursor: pointer;
}




//|||||||||||||||||||COMMON ELEMENTS|||||||||||||||||||//
.main-btn {
	background: #ff9c00;
	font-size: 15px;
	border-radius: 50px;
	width: 	100%;
	max-width: 280px;
	text-align: center;
	padding-top: 21px;
	padding-bottom: 21px;
}

.h2-title {
	font-size: (36vw/1170*100);
	font-weight: 500;
	text-align: center;
}


//|||||||||||||||||||END COMMON ELEMENTS|||||||||||||||||||//








