.exclusive {
	background: url(../img/bg.jpg) no-repeat center;
	background-size: cover;
	color: #ffffff;
	padding-top: (170%/1920*100);
	padding-bottom: 80px;
	.f-container {
		flex-direction: column;
	}
}

.exclusive__title {
	font-size: (44vw/1170*100);
}


.exclusive__top {
	.flex();
}

.exclusive__form-wrap {
	max-width: 388px;
	background: #fff;
	border-radius: 7px;
	padding: 65px 47px;
}

.exclusive__form-title {
	margin-bottom: 35px;
	color: #242424;
	text-align: center;
	font-size: (26vw/1170*100);
}

.exclusive__left, .exclusive__top {
	max-width: 100%;
}

.exclusive-slider, .exclusive-slide {
	max-width: 100%;
	.flex();
}

.exclusive-slider {
	

}

.exclusive-slide {
	.exclusive__btn {
		margin-top: 41px;
	}
}

.exclusive__left {
	flex: 0 1  60%;
	max-width: 55%;
}

.exclusive__right {
	flex: 0 1 425px;
}

.exclusive__form-input {
	text-align: left;
	border-bottom: 1px solid #c7c7c7;
	&:first-child{
		margin-bottom: 37px;
	}

	&:first-child + & {
		margin-bottom: 51px;
	}

}

.exclusive__form {
	.exclusive__btn {
		margin: 0 auto;
	}
}

.exclusive__bottom {
	position: relative;
	width: 100%;
	&:before {
		display: block;
		content: "";
		padding-top: 15%;
	}
	.slick-dots {
		display: flex;
		justify-content: center;
		li {
			margin-right: 2px;
		}
	}
	.slick-dots li button:before {
		content: "";
		width: 13px;
		height: 13px;
		border-radius: 50%;
		border: 1px solid #fff;
		opacity: 1;
	}

	.slick-dots li.slick-active button:before {
		background: #ff9c00;
		border: none;
	}
}