.calculator {
	background:#f2f2f2;
	padding-top: 95px;
	padding-bottom: 90px;
}

.calculator__title {
	margin-bottom: 110px;
}

.calculator__left {
	flex: 0 1 63.5%;
}

.calculator__right {
	flex: 0 1 30%;
}

.calculator__left-top {
	.flex();
	margin-bottom: 76px;
}

.calculator__breed, .calculator__type {
	display: flex;
	width: 50%;
	align-items: center;
	max-width: 271px;
}

.calculator__arrow-holder {
	margin-left: 15px;
	display: block;
	max-width: 10px;
	width: 100%;
	max-height: 8px;
}

.calculator__list-handler {
	cursor: pointer;
	font-size: 15px;
	position: relative;
	.calculator__sublist {
		display: none;
		position: absolute;
		width: 	100%;
		max-width: 230px;
		top: 110%;
		background: #fff;
		box-shadow: 0 2px 10px -6px rgba(0,0,0,.45);
		text-align: center;
		padding: 25px;
	}

}

.calcularor__list-item-breed, .calcularor__list-item-type {
	transition: all 0.25s linear;
	&:not(:last-child){
		margin-bottom: 	26px;
	}
	cursor: pointer;
	&:hover {
		color: #b1a26d;
	}
}

.calculator__text-field {
	padding-bottom: 7px;
	font-size: 15px;
	background: transparent;
	border-bottom: 1px solid #d9d9d9;
	&::-webkit-input-placeholder {
		color: #7e7e7e;
	}

	&::-moz-placeholder {
		color: #7e7e7e;
	}
	color:#7e7e7e;
	text-align: left;
	max-width: 271px;
}


.calculator__form {
	.flex();
}


.rotate-arrow{
	transform: rotate(180deg);
}

.calculator__btn {
	margin: 0 auto;
	margin-top: 61px;
}

.calculator__range-wrap {
	.flex();
	&:not(:last-child) {
		margin-bottom: 57px;
	}
}


.ui-slider-horizontal .ui-slider-range-min {
	background: #9e8b48;
}


.ui-widget.ui-widget-content {
	border: none;
	height: 3px;
	max-width: 257px;
	width: 100%;
	background: #d9d9d9;
}


.ui-slider-horizontal .ui-slider-handle {
	top: -0.2em;
}

.ui-state-default, .ui-widget-content .ui-state-default {
	background: #9e8b48;
	width: 9px;
	height: 9px;
	border-radius: 50%;
	border: none;
	outline: none;

}

.ui-slider .ui-slider-handle:after {
    content: attr(data-text);
    position: absolute;
    top: -25px;
    width: 54px;
    font-size: 14px;
    text-align: center;
    left: -22px;
    font-weight: 300;
    color: #242424;
}